@import '~antd/dist/antd.css';

canvas {
  width: 100%;
  height: 100%;
}

.control-panel {
   box-shadow:
  0 2.8px 2.2px rgba(0, 0, 0, 0.034),
  0 6.7px 5.3px rgba(0, 0, 0, 0.048),
  0 12.5px 10px rgba(0, 0, 0, 0.06),
  0 22.3px 17.9px rgba(0, 0, 0, 0.072),
  0 41.8px 33.4px rgba(0, 0, 0, 0.086),
  0 100px 80px rgba(0, 0, 0, 0.12)
;
  background-color: rgba(255,255,255,0.95);
  border: 1px solid rgba(240,240,240,0.95);
  
  width: 200px;
  position: fixed;
  z-index: 1;
  right: 0;
  top: 5%;
}


.charge-slider .ant-slider-track{
  background-color: transparent;
}
.charge-slider .ant-slider:hover .ant-slider-track {
  background-color: transparent;
}
.charge-slider {
  position: relative;
  padding: 0px 30px;
}

.charge-slider .anticon {
  position: absolute;
  top: -2px;
  width: 16px;
  height: 16px;
  line-height: 1;
  font-size: 16px;
  color: rgba(0, 0, 0, 0.25);
}

.charge-slider .charge-slider-active {
  color: rgba(0, 0, 0, 0.45);
}

.charge-slider .anticon:first-child {
  left: 0;
}

.charge-slider .anticon:last-child {
  right: 0;
}